body {
  .swiper-pagination-bullet {
    width: 20px;
    height: 20px;
    opacity: 1;
    background: #def3ff;
    margin: 0 !important;
    &:not(:last-child) {
      margin-right: 33px !important;
    }

    &.swiper-pagination-bullet-active {
      background: #8aceee;
    }
  }
}
.cloud-zone {
  background: #DEF3FF;
  background-blend-mode: multiply;
  position: relative;
  overflow: hidden;
}
.cloud {
  position: absolute;
  z-index: 0;
  pointer-events: none;


  &.--cloud-1 {
  right: -1500px;
  top: -250px;
  }
  &.--cloud-2 {
    left: -500px;
    top: 190px;
  }
  &.--cloud-3 {
    right: -800px;
    top: 1800px;
  }
  &.--cloud-4 {
    left: -1500px;
    bottom: 800px;
  } 
  &.--cloud-5 {
    right: -500px;
    bottom: -200px;
  } 

}

.about {
  padding: 50px 0 65px;
  position: relative;
  z-index: 10;
  .about__wrapper {
    // padding: 0 132px;
  }
  .about__header {

  }
  .about__text {
    font-size: 35px;
    font-weight: 300;
    line-height: 140%;
    letter-spacing: 0.4399999976158142px;
    text-align: center;
    color: #626261;
  }
  .about__benefits {
    display: flex;
    justify-content: space-between;
    align-items: end;
  }
  .about__benefit {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .about__benefit-icon {
    object-fit: contain;
  }
  .about__benefit-desc {
    font-size: 29px;
    font-weight: 600;
    line-height: 90px;
    letter-spacing: 1.440000057220459px;
    text-align: center;
    color: #008cd3;
  }
}

.good {
  padding: 93px 0 0;
  position: relative;
  z-index: 1;
  &:last-child {
    padding-bottom: 90px;
  }
  .good__wrapper {
    padding: 0 132px;
  }
  .good__header {

  }
  .good__main-img {
    width: 100%;
    border-radius: 40px;
    overflow: hidden;
    img {
      width: 100%;
    }
  }
  .good__slider-wrapper {
    margin-top: 90px;
    position: relative;
  }
  .good__slider {
    padding: 50px;
    margin: 0 -50px;
  }
  .good__slide {
    padding: 60px 60px 32px;
    background: #fff;
    border-radius: 40px;
    transition: .3s;

    // &.swiper-slide-next {
    //   box-shadow: 20px 20px 40px 0px #BCE0F5B5;
    // }
    &:hover {
      box-shadow: 20px 20px 40px 0px #BCE0F5B5;
    }

    &.--mini {
      padding: 60px 32px 32px;
      .good__slide-name {
        font-size: 20px;
      }
      .good__slide-desc {
        font-size: 20px;
      }
    }
  }
  .good__slide-img {
    img {
      margin: 0 auto;
    }
  }
  .good__slide-name {
    font-size: 29px;
    font-weight: 400;
    line-height: 130%;
    letter-spacing: 1.440000057220459px;
    text-align: center;
    color: #008cd3;
    text-transform: uppercase;
    display: block;
  }
  .good__slide-desc {
    font-size: 25px;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: 1.440000057220459px;
    text-align: center;
    color: #008cd3;
    text-transform: lowercase;
  }
  .good__slider-nav {

  }
  .good__slider-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    box-shadow: 6px 11px 16px 0px #BCE0F5B5;
    border-radius: 50%;
  }
  .good__slider-btn {
    cursor: pointer;
    &.--prev {
      transform: scale(-1, 1) translateY(-50%);
      left: -135px;
    }
    &.--next {
      right: -135px;
    }
  }
}

.mob-text {
  display: none;
}

